'use client'

import { useState, useEffect } from 'react';
import { FaMoon, FaSun, FaClock } from 'react-icons/fa';

export default function DateTimeCard() {
  const [date, setDate] = useState(new Date());
  const [hijriDate, setHijriDate] = useState({ date: '', monthName: '' });
  const [moonPhase, setMoonPhase] = useState({ phase: '', icon: 'ðŸŒ‘' });

  useEffect(() => {
    const timer = setInterval(() => setDate(new Date()), 1000);

    // Get Hijri date
    const getHijriDate = async () => {
      try {
        const response = await fetch('http://api.aladhan.com/v1/gToH', {
          method: 'GET',
          params: {
            date: date.toLocaleDateString('en-US', { 
              day: '2-digit', 
              month: '2-digit', 
              year: 'numeric' 
            })
          }
        });
        const data = await response.json();
        setHijriDate({
          date: data.data.hijri.date,
          monthName: data.data.hijri.month.en
        });
      } catch (error) {
        console.error('Error fetching Hijri date:', error);
      }
    };

    // Get moon phase from RapidAPI with location
    const getMoonPhase = async () => {
      try {
        const response = await fetch('https://moon-phase.p.rapidapi.com/advanced?lat=51.4768&lon=-0.0004', {
          method: 'GET',
          headers: {
            'x-rapidapi-host': 'moon-phase.p.rapidapi.com',
            'x-rapidapi-key': '695b305d2amsh3d1dd9909c2adc4p124df5jsn15c44a15f253'
          }
        });
        const data = await response.json();
        
        // Map moon phase to icon and name
        const getMoonIcon = (phase: number) => {
          if (phase < 0.125) return { phase: 'New Moon', icon: 'ðŸŒ‘' };
          if (phase < 0.25) return { phase: 'Waxing Crescent', icon: 'ðŸŒ’' };
          if (phase < 0.375) return { phase: 'First Quarter', icon: 'ðŸŒ“' };
          if (phase < 0.5) return { phase: 'Waxing Gibbous', icon: 'ðŸŒ”' };
          if (phase < 0.625) return { phase: 'Full Moon', icon: 'ðŸŒ•' };
          if (phase < 0.75) return { phase: 'Waning Gibbous', icon: 'ðŸŒ–' };
          if (phase < 0.875) return { phase: 'Last Quarter', icon: 'ðŸŒ—' };
          return { phase: 'Waning Crescent', icon: 'ðŸŒ˜' };
        };

        // Get current phase from advanced API data
        const phaseValue = data.currentPhase / 360; // Convert degrees to 0-1 range
        setMoonPhase(getMoonIcon(phaseValue));

      } catch (error) {
        console.error('Error fetching moon phase:', error);
        // Fallback to calculation method
        const phase = ((date.getTime() / 86400000 + 2.5) % 29.53059) / 29.53059;
        if (phase < 0.125) setMoonPhase({ phase: 'New Moon', icon: 'ðŸŒ‘' });
        else if (phase < 0.25) setMoonPhase({ phase: 'Waxing Crescent', icon: 'ðŸŒ’' });
        else if (phase < 0.375) setMoonPhase({ phase: 'First Quarter', icon: 'ðŸŒ“' });
        else if (phase < 0.5) setMoonPhase({ phase: 'Waxing Gibbous', icon: 'ðŸŒ”' });
        else if (phase < 0.625) setMoonPhase({ phase: 'Full Moon', icon: 'ðŸŒ•' });
        else if (phase < 0.75) setMoonPhase({ phase: 'Waning Gibbous', icon: 'ðŸŒ–' });
        else if (phase < 0.875) setMoonPhase({ phase: 'Last Quarter', icon: 'ðŸŒ—' });
        else setMoonPhase({ phase: 'Waning Crescent', icon: 'ðŸŒ˜' });
      }
    };

    getHijriDate();
    getMoonPhase();
    return () => clearInterval(timer);
  }, [date]);

  return (
    <div className="max-w-sm mx-auto bg-white/90 backdrop-blur-md rounded-xl shadow-lg overflow-hidden transform-gpu hover:scale-105 transition-transform duration-200">
      <div className="p-6 space-y-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            {date.getHours() >= 6 && date.getHours() < 18 ? 
              <FaSun className="text-yellow-500 w-6 h-6" /> : 
              <FaMoon className="text-blue-500 w-6 h-6" />
            }
            <FaClock className="text-gray-600 w-5 h-5" />
          </div>
          <span className="text-2xl font-bold text-gray-800">
            {date.toLocaleTimeString('en-US', { 
              hour: '2-digit', 
              minute: '2-digit', 
              hour12: true 
            })}
          </span>
        </div>
        
        <div className="space-y-2">
          <p className="text-lg text-gray-700">
            {date.toLocaleDateString('en-US', { 
              weekday: 'long', 
              year: 'numeric', 
              month: 'long', 
              day: 'numeric' 
            })}
          </p>
          <p className="text-md text-gray-600">
            Hijri: {hijriDate.date} 
            <span className="text-gray-500 ml-2">({hijriDate.monthName})</span>
          </p>
          <div className="text-md text-gray-600 flex items-center space-x-2">
            <span>Moon Phase:</span>
            <span className="flex items-center gap-2">
              <span className="text-xl">{moonPhase.icon}</span>
              <span>{moonPhase.phase}</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}