'use client'

import { 
  FaFacebook, 
  FaYoutube, 
  FaInstagram, 
  FaTwitter, 
  FaReddit,
  FaGithub,
  FaMedium,
  FaQuora,
  FaGlobe,
} from 'react-icons/fa';
import { SiBluesky, SiThreads } from 'react-icons/si';
import DateTimeCard from '../shared/DateTimeCard';

export default function Footer() {
const socialLinks = [
    { name: 'Facebook', icon: FaFacebook, url: 'https://www.facebook.com/foxaleo' },
    { name: 'YouTube', icon: FaYoutube, url: 'https://www.youtube.com/@felixarash' },
    { name: 'Instagram', icon: FaInstagram, url: 'https://www.instagram.com/felixarash' },
    { name: 'X (Twitter)', icon: FaTwitter, url: 'https://twitter.com/felixarash' },
    { name: 'BlueSky', icon: SiBluesky, url: 'https://bsky.app/profile/felixarash' },
    { name: 'Reddit', icon: FaReddit, url: 'https://www.reddit.com/user/felixarash' },
    { name: 'GitHub', icon: FaGithub, url: 'https://github.com/felixarash' },
    { name: 'Medium', icon: FaMedium, url: 'https://medium.com/@felixarash' },
    { name: 'Quora', icon: FaQuora, url: 'https://www.quora.com/profile/felixarash' },
    { name: 'Threads', icon: SiThreads, url: 'https://www.threads.net/@felixarash' },
    { name: 'Website', icon: FaGlobe, url: '/' }
];

  return (
    <footer className="w-full mt-auto border-t border-gray-200 bg-white/80 backdrop-blur-sm transform-gpu">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-8 sm:py-12">
        {/* DateTime Card */}
        <div className="mb-8">
          <DateTimeCard />
        </div>

        {/* Social Links */}
        <div className="flex flex-wrap justify-center gap-6 sm:gap-8 mb-8">
          {socialLinks.map((social) => (
            <a
              key={social.name}
              href={social.url}
              target={social.name === 'Portfolio' || social.name === 'Website' ? '_self' : '_blank'}
              rel="noopener noreferrer"
              className="group p-2 rounded-lg hover:bg-gray-100/50 transition-all duration-200"
              aria-label={social.name}
            >
              <span className="relative w-6 h-6 sm:w-8 sm:h-8 block transform group-hover:scale-110 transition-transform duration-200">
                <social.icon className="w-full h-full text-gray-600 group-hover:text-gray-900 transition-colors" />
              </span>
            </a>
          ))}
        </div>

        {/* Copyright */}
        <div className="pt-8 border-t border-gray-200">
          <p className="text-center text-sm text-gray-600">
            Â© {new Date().getFullYear()} Felix Arash. All rights reserved.
          </p>
        </div>
      </div>
    </footer>
  );
}